  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.13;
}

.header-logo:hover, .header-logo:focus {
  text-decoration: underline;
}

.nav-list {
  align-items: center;
  gap: 16px;
  display: flex;
}

.checkbox {
  width: 46px;
  --circul-el-color: #f8f8f8;
  height: 20px;
  border-radius: 10px;
  align-items: center;
  display: flex;
  position: relative;
}

.checkbox-inner {
  cursor: pointer;
  background-color: var(--additional-color);
  width: 46px;
  height: 20px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
  z-index: 1000;
  border-radius: 35px;
  position: absolute;
  top: 0;
  left: 0;
}

.checkbox-inner:before {
  content: "";
  width: 18.8px;
  height: 18.8px;
  background-color: var(--secondary-color);
  z-index: 100;
  transition: background-color var(--transition-duration) var(--transition-time-function) transform var(--transition-duration) var(--transition-time-function);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.checkbox-input:checked + .checkbox-inner:before {
  background-color: #fff;
  transform: translateX(26px);
}

.checkbox-input:checked + .checkbox-inner {
  background-color: var(--active-color);
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
}

@media screen and (max-width: 767px) {
  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .header-icon-wrapper {
    align-items: center;
    gap: 14px;
  }

  .nav-menu, .switch-checkbox-wrapper {
    display: none;
  }

  .burger-menu {
    height: 32px;
    padding-left: 0;
    padding-right: 0;
  }

  .mobile-close-icon {
    stroke: var(--circul-el-color);
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .mobile-menu {
    width: 50%;
    height: 100vh;
    z-index: 1000;
    background-color: var(--active-color);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity var(--transition-duration) var(--transition-time-function) visibility var(--transition-duration) var(--transition-time-function);
    background-color: var(--new-el-color);
    padding-top: 200px;
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-backdrop {
    width: 100%;
    height: 100%;
    z-index: 100;
    opacity: 1;
    transition: opacity var(--transition-duration) var(--transition-time-function), visibility var(--transition-duration) var(--transition-time-function);
    background-color: rgba(82, 80, 80, .3);
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-backdrop.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }

  .mobile-menu-close-btn {
    height: 32px;
    display: block;
  }

  .mobile-nav-list {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .mobile-nav-link {
    color: var(--secondary-color);
    transition: color var(--transition-duration) var(--transition-time-function), border-bottom var(--transition-duration) var(--transition-time-function);
    color: var(--link-el-color);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.2;
  }

  .mobile-nav-link:hover, .mobile-nav-link:focus {
    border-bottom: 1px solid #fff;
  }

  .mobile-nav-link.current {
    color: #fff;
    font-weight: 500;
  }

  .mobile-menu {
    transition: transform var(--transition-duration) var(--transition-time-function);
    transform: translateX(-100%);
  }

  .mobile-menu.is-open {
    transform: translateX(0);
  }

  .checkbox {
    position: absolute;
    bottom: 20px;
    left: 20px;
  }

  .checkbox-inner:before {
    background-color: var(--circul-el-color);
  }
}

